import {CartoLayer,CartoSQLLayer, MAP_TYPES, setDefaultCredentials,BASEMAP} from '@deck.gl/carto';
import {TextLayer} from '@deck.gl/layers';
import React, {useState, useCallback,useEffect} from 'react';
import {render} from 'react-dom';
import {StaticMap} from 'react-map-gl';
import DeckGL from '@deck.gl/react';
import { LEGEND_COLORS } from './barChart';
import axios from 'axios';



const INITIAL_VIEW_STATE = {
    latitude: -33.878197,
    longitude: 151.148648,
    zoom:10
  };


  const credentials = {
    username: "gsc-admin",
    apiKey: "default_public"
  };
 
  //const rows =  await fetchData();
  //console.log(rows[0]); // {count: 7343}

setDefaultCredentials(credentials);




const MAPBOX_ACCESS_TOKEN = 'pk.eyJ1IjoiYnNuYXlhbiIsImEiOiJja3Ewczd3MW4wN3Z3MnVvNDNsenlzNnFsIn0.f8nk8yrxIeCKOlzIy1b-Pg'

export const POLYGON_COLORS ={
    COLOR_YES: [0,0,0,0],
    COLOR_NO :[248,15,54],
    COLOR_GREEN :[16,196,118]
  }
  
const AccessMap = ()=> {

  const [data ,setData] = useState([]);

const query ='SELECT ST_X(ST_Centroid(the_geom)) x, ST_Y(ST_CENTROID(the_geom)) y,district from districtboundary';
useEffect( ()=>{
  const getData = async ()=> {
    
    const response  = await axios.get(`https://gsc-admin.carto.com/api/v2/sql?q=${encodeURIComponent(query)}`);
    setData(response.data.rows.map(elm=>{
      const d= {text:elm.district,position:[elm.x,elm.y]}
      //console.log(d);
      return {text:elm.district,position:[elm.x,elm.y]}
    })
    )
    return response;
    
  }
  getData();

},[query])

//onsole.log(data);


  
const texLayer = new TextLayer({
  id: 'text-layer',
  data,
  pickable: false,
  sizeMinPixels:2,
  sizeMaxPixels:10,

  getSize: 32,
  getAngle: 0,
  getTextAnchor: 'middle',
  getAlignmentBaseline: 'center',
  getBackgroundColor:[255,255,255]
});

   
     
  
    const layerAccess = new CartoLayer({
      id:'access-uni',
      type: MAP_TYPES.QUERY,
      data: 'select the_geom_webmercator, network_infrastructure as type, quality from nbn_performance',
    
      getFillColor: object =>{
        
        return object.properties.quality==='Others'?[...LEGEND_COLORS[object.properties.quality],100]:[...LEGEND_COLORS[object.properties.quality],200]
      },
      getLineColor: object =>LEGEND_COLORS[object.properties.quality],
      lineWidthMinPixels: .10,
      pickable: true,
      
    });



    const layerDistrict = new CartoLayer({
      id:'district',
      type: MAP_TYPES.QUERY,
      data: `select the_geom_webmercator, district from "gsc-admin".districtboundary `,
    
      getFillColor: [0,0,0,0],
      getLineColor: [35, 35, 35],
      lineWidthMinPixels: 1.2,
      
      pickable: false,
      
    });

    
  
    const getTooltip = ({object}) => {
      if (!object) return false;
      const {type,quality} = object.properties;
      
  
      return {
        html: `<div>Network infrastructure: ${type}</div>
        <div>Performance: ${quality}</div>`,
        
        
        style:{
          
          fontSize: '0.8em'
         
        }
      }
    };

  
    return (
      <div>
       
        <DeckGL
          width="100%"
          height="100%"
          initialViewState={INITIAL_VIEW_STATE}
          getTooltip = {getTooltip}
          controller={true}
          layers={[layerAccess,layerDistrict,texLayer]}
        >
          <StaticMap
            reuseMaps
            mapStyle= {BASEMAP.PRISTON}//"mapbox://styles/mapbox/dark-v9"
            mapboxApiAccessToken= {MAPBOX_ACCESS_TOKEN}
            preventStyleDiffing
          />
        </DeckGL>
      </div>
    );
  }


  export default AccessMap;